.tb_sb__b_cont {
       padding: 10px;
       background-color: #fafafa;
}

.tb_sb__b_wrapper {
       display: flex;
       align-items: center;
       width: 100%;
}

.tb_sb__profile {
       overflow: hidden;
       display: flex;
       align-items: center;
       flex-grow: 1;
       width: 100%;
}

.tb_sb__brand {
       margin-right: 10px;
       position: relative;
}

.tb_sb__brand_icon {
       background-position: 50%;
       background-repeat: no-repeat;
       background-size: cover;
       border-radius: 50%;
       display: flex;
       flex-shrink: 0;
       height: 64px;
       overflow: hidden;
       width: 64px;
       align-items: center;
       background-color: #fafafa;
       justify-content: center;
       padding: 5px;
}

.tb_sb__brand_icon:after,
.tb_sb__brand_icon:before {
       border-radius: 50%;
       content: "";
       position: absolute;
}

.tb_sb__brand_icon:after {
       background: #EC8A46;
       background: linear-gradient(90deg, #EC8A46, 50%, #469AEA);
       filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#EC8A46", endColorstr="#469AEA", GradientType=1);
       height: 100%;
       left: 0;
       top: 0;
       width: 100%;
}

.tb_sb__brand_icon:before {
       background: #fff;
       height: calc(100% - 4px);
       left: 2px;
       top: 2px;
       width: calc(100% - 4px);
       z-index: 1;
}

.tb_brand_img {
       height: 100% !important;
       margin: 0;
       object-fit: cover;
       object-position: center;
       width: 100% !important;
       position: relative;
       z-index: 1;
       border-radius: 50%;
}

.tb_sb___info {
       display: flex;
       flex-direction: column;
       align-items: start;
       justify-content: start;
       flex-grow: 1;
       margin-right: 15px;
       width: 100%;
       overflow: hidden;
}

.tb_sb__name {
       color: #000;
       display: block;
       font-family: inherit;
       font-size: 18px;
       font-weight: 600;
       line-height: normal;
       margin-bottom: 4px;
       overflow: hidden;
       text-align: left;
       text-overflow: ellipsis;
       white-space: nowrap;
       width: 100%;
}

.tb_sb__posts {
       font-size: 14px;
       font-weight: 500;
       opacity: 0.7;
       display: block;
       line-height: 1.5;
       overflow: hidden;
       line-height: normal;
       text-overflow: ellipsis;
       white-space: nowrap;
       width: 100%;
}

.tb_sb_f_btn {
       font-family: inherit;
       padding: 8px 20px;
       color: #fff;
       background-color: #0095F6;
       border-radius: 4px;
       font-weight: 400;
       text-align: center;
       display: flex;
       align-items: center;
       justify-content: center;
       border-width: 1px;
       border-style: solid;
       font-size: 14px;
       line-height: normal;
       cursor: pointer;
       margin: 0 auto;
       position: relative;
       min-width: 80px;
       transition: opacity .3s ease-in-out;
}

.tb_sb_f_btn:hover {
       opacity: 0.9;
}

.tb_sb_btn_wrap_ {
       display: flex;
       flex-shrink: 0;
}

.tb_sb_f_wrapper {
       display: flex;
       align-items: start;
       flex-wrap: wrap;
       justify-content: start;
       position: relative;
       margin-top: 10px;
}

.tb_sb_f_list {
       padding: 6px;
       position: relative;
}

.tb_sb_f_button {
       height: 34px;
       display: flex;
       align-items: center;
       justify-content: center;
       text-decoration: none;
       text-align: center;
       background-color: #fff;
       color: #000;
       -webkit-transition: all .3s ease;
       transition: all .3s ease;
       cursor: pointer;
       position: relative;
       z-index: 2;
       width: auto;
       padding: 7px 10px;
       border-radius: 4px;
       border-style: solid;
       border-width: 1px;
       border-color: #000;
}

.tb_sb_f_list.tb_active .tb_sb_f_button {
       background-color: #000;
       color: #fff;
}

.tb_sb_f_icon {
       font-size: 16px;
       line-height: 20px;
       position: relative;
}

.tb_sb_f_ico {
       margin-right: 5px;
}

.tb_sb_f_ico,
.tb_colr_ico:before {
       -webkit-transition: all .3s ease;
       transition: all .3s ease;
}

.tb_sb_f_button_text {
       font-family: inherit;
       font-size: 14px;
       font-weight: 400;
       line-height: normal;
}

.tb_sb_btn_wrap_ {
       position: relative;
       margin: 10px 0;
}

.tb_sb_dropdown_ {
       background-color: #ffffff;
       margin-top: 5px;
       border-radius: 3px;
       min-width: 160px;
       position: absolute;
       text-align: left;
       z-index: 99;
       top: 130%;
       right: 0;
       opacity: 0;
       visibility: hidden;
       overflow-x: auto;
       max-height: 245px;
       box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
       transition: all .3s ease-in-out;
}

.tb_sb_dropdown_.tb_sb_dd_show {
       top: 100%;
       opacity: 1;
       visibility: visible;
}

.tb_sb_dropdown_title {
       font-size: 12px;
       color: #111111;
       padding: 8px 15px;
       margin-top: 10px;
       line-height: 1.5;
}

.tb_sb_dd_list {
       display: flex;
       align-items: center;
       white-space: nowrap;
       font-size: 13px;
       font-weight: 500;
       padding: 10px 15px;
       cursor: pointer;
       position: relative;
       transition: background-color .3s ease-in-out;
}

.tb_sb_dd_list:after {
       content: '';
       height: 1px;
       background-color: rgba(0, 0, 0, .02);
       width: 100%;
       position: absolute;
       bottom: 0;
       left: 0;
}

.tb_sb_dd_list:hover {
       background-color: rgba(33, 142, 255, .08);
}